import { useSelector, useDispatch } from 'react-redux';
import { useState } from 'react';

import CartItem from './CartItem/CartItem';
import { cartSliceActions } from '../../../store/cart-slice';

import classes from './Cart.module.css';
import Modal from '../../Common/Modal/Modal';

function Cart({
    history
}) {
    const [proccedToCheckout, setProccedToCheckout] = useState(null);

    const cartItems = useSelector(state => state.cart.items);
    const totalQuantity = useSelector(state => state.cart.totalQuantity);
    const totalPrice = useSelector(state => state.cart.totalPrice);

    const dispatch = useDispatch();

    if (proccedToCheckout !== null) {
        return (
            <Modal
                message={proccedToCheckout}
                closeModalClickHandler={() => {
                    dispatch(cartSliceActions.removeCartItems());
                    history.push('/');
                }}
            />
        )
    }
    return (
        <div className={classes.cartWrapper}>
            {
                cartItems.map((cartItem, index) => {
                    return <CartItem key={index} {...cartItem} />
                })
            }
            <div className={classes.summary}>
                <h4 className={classes.summaryTitle}>Cart Summary</h4>
                <div className={classes.summaryDetails}>
                    <span className={classes.totalItems}>TOTAL: ({totalQuantity} items)</span>
                    <span className={classes.totalPrice}>$ {totalPrice}</span>
                </div>
                <button onClick={() => setProccedToCheckout('YOUR ORDER HAS BEEN ACCEPTED!')} className={classes.summaryBtn}>
                    Proceed To Checkout
                </button>
            </div>
        </div>
    )
}

export default Cart;