import style from '../Decorate.module.css';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';

import { getSpecificMovieArticle, updateMovieArticle } from '../../../../utils/requests'

function Edit() {
    const [details, setDetails] = useState({});
    const { movieId } = useParams();
    const history = useHistory();
    const authToken = useSelector(state => state.auth.userAuthState.authToken);

    useEffect(() => {
        return getSpecificMovieArticle(movieId).then(setDetails);
    }, [movieId]);

    const onEditMovieArticleSubmitHandler = async (e) => {
        e.preventDefault();
        const { title, description, img, price } = e.target;

        return updateMovieArticle(movieId, authToken, {
            title: title.value,
            description: description.value,
            img: img.value,
            price: Number(price.value),
        })
            .then(history.push(`/movies/details/${movieId}`));
    }

    return (
        <section className={style.createPage}>
            <div className={style.createFormWrapper}>
                <h2 className={style.createFormTitle}>EDIT MOVIE ARTICLE</h2>
                <form onSubmit={onEditMovieArticleSubmitHandler}>
                    <input className={style.movieTitle}
                        type="text"
                        name="title"
                        placeholder="ENTER MOVIE TITLE"
                        defaultValue={details.title}
                    />
                    <textarea className={style.movieDescription}
                        type="text"
                        name="description"
                        placeholder="ENTER MOVIE DESCRIPTION"
                        rows="4"
                        columns="50"
                        defaultValue={details.description}
                    >
                    </textarea>
                    <input className={style.movieImageURL}
                        type="text"
                        name="img"
                        placeholder="ENTER MOVIE IMAGE URL"
                        defaultValue={details.img}
                    />
                    <input className={style.moviePrice}
                        type="number"
                        name="price"
                        placeholder="ENTER MOVIE PRICE"
                        defaultValue={details.price}
                    />
                    <button className={style.createMovieBtn} type="submit">EDIT</button>
                </form>
            </div>
        </section>
    )
}

export default Edit;