import * as api from './api';

export async function getMovieArticles() {
    return await api.get('/data/movies');
}

export async function postMovieArticle(authToken, data) {
    return await api.post('/data/movies', authToken, data);
}

export async function updateMovieArticle(movieId, authToken, data) {
    return await api.put(`/data/movies/${movieId}`, authToken, data);
}

export async function deleteMovieArticle(movieId, authToken) {
    return await api.del(`/data/movies/${movieId}`, authToken);
}

export async function getMovieArticleLikesCount(movieId) {
    return await api.get(`/data/likes?where=movieId%3D%22${movieId}%22&distinct=_ownerId&count`)
}

export async function getMovieArticleCommentsCount(movieId) {
    return await api.get(`/data/comments?where=movieId%3D%22${movieId}%22&distinct=_ownerId&count`);
}

export async function getSpecificMovieArticle(movieId) {
    return await api.get(`/data/movies/${movieId}`);
}

export async function getMovieArticleLikesFromUser(movieId, userId) {
    return await api.get(`/data/likes?where=movieId%3D%22${movieId}%22%20and%20_ownerId%3D%22${userId}%22`)
}

export async function postMovieArticleLike(authToken, data) {
    return await api.post('/data/likes', authToken, data);
}

export async function getMovieArticleComments(movieId) {
    return await api.get(`/data/comments?where=movieId%3D%22${movieId}%22`)
}

export async function postMovieArticleComment(authToken, data) {
    return await api.post('/data/comments', authToken, data);
}

export async function deleteMovieArticleComment(commentId, authToken) {
    return await api.del(`/data/comments/${commentId}`, authToken);
}

export async function logout() {
    return await api.get('/users/logout');
}

export async function getCartItems() {
    return await api.get('/data/cart');
}

export async function addCartItem(authToken, data) {
    return await api.post('/data/cart', authToken, data);
}

export async function deleteCartItem(cartId, authToken) {
    return await api.del(`/data/cart/${cartId}`, authToken);
}

export async function updateCartStore(authToken, data) {
    return await api.put('/data/cart/9Rxf5qvVFHR0YcFpZgP5', authToken, data);
}