import { useSelector } from 'react-redux';
import classes from './DetailsButtons.module.css';
import { useContext, Fragment } from 'react';


import { DetailsContextProvider } from '../Details';

import OwnerButtons from './OwnerButtons/OwnerButtons';
import UserButtons from './UserButtons/UserButtons';

function DetailsButtons() {
    const { movieArticle } = useContext(DetailsContextProvider);

    const currentAuthState = useSelector(state => state.auth);
    const authToken = currentAuthState.userAuthState.authToken;


    return (
        <div className={classes.movieDetailsButtonWrapper}>
            {
                !currentAuthState.isAuthenticated
                    ? null
                    : currentAuthState.userAuthState.userId !== movieArticle._ownerId
                        ?
                        <Fragment>
                            <UserButtons currentAuthState={currentAuthState} />
                        </Fragment>
                        :
                        <Fragment>
                            <OwnerButtons authToken={authToken} movieId={movieArticle._id} />
                        </Fragment>
            }
        </div>
    );
}

export default DetailsButtons;