import { useRef, useContext } from 'react';
import { useSelector } from 'react-redux';
import { DetailsContextProvider } from '../../Details';

import { postMovieArticleComment } from '../../../../../utils/requests';
import { REDUCER_ACTIONS } from '../../reducer';

import classes from './CommentForm.module.css';


function CommentForm() {
    const content = useRef('');
    const authState = useSelector(state => state.auth);
    const { movieArticle, dispatch } = useContext(DetailsContextProvider);

    const postNewComment = async(e) => {
        e.preventDefault();

        let contentVal = content.current.value;

        postMovieArticleComment(authState.userAuthState.authToken, {
            author: authState.userAuthState.username,
            content: contentVal,
            movieId: movieArticle._id
        }).then(res => {
                console.log(res);
                content.current.value = '';
                dispatch({ type: REDUCER_ACTIONS.MODIFY_MOVIE_ARTICLE_COMMENT })
            });
    }
    return (
        (authState.userAuthState.userId !== movieArticle._ownerId && authState.isAuthenticated) &&
        (<form onSubmit={postNewComment}>
            <textarea className={classes.commentInput}
                name="content"
                rows="4"
                cols="50"
                placeholder="Enter your expression from this movie"
                ref={content}
            >
            </textarea>
            <button className={classes.commentSubmit} type="submit">Post comment</button>
        </form>)
    );
}


export default CommentForm;