import './App.css';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { authSliceActions } from './store/auth-slice';

import { BrowserRouter as Router } from 'react-router-dom';

import Header from './components/Header/Header';
import Main from './components/Main/Main';

function App() {
    const dispatch = useDispatch();
    const authState = useSelector(state => state.auth); // { isAuthenticated: false, userAuthState: {} }

    useEffect(() => {
        const authStateFromLocalStorege = JSON.parse(localStorage.getItem('user')) || {
            userAuthState: {
                authToken: null,
                userId: null,
                username: null
            },
            isAuthenticated: false
        }

        dispatch(authSliceActions.setUserState({
            isAuthenticated: authStateFromLocalStorege.isAuthenticated,
            userAuthState: authStateFromLocalStorege.userAuthState
        }));
    }, [dispatch]);


    useEffect(() => {
        localStorage.setItem('user', JSON.stringify(authState));
    }, [authState]);

    return (
        <Router>
            <Header />
            <Main />
        </Router>
    );
}

export default App;
