import { useEffect, useReducer, Fragment, createContext } from 'react';

import { useParams } from 'react-router-dom';
import classes from './Details.module.css';

import CommentSection from './CommentSection/CommentSection';
import DetailsButtons from './DetailsButtons/DetailsButtons';

import reducer, { INITIAL_MOVIE_DETAILS_STATE, REDUCER_ACTIONS } from "./reducer";

import Loader from '../../Common/Loader/Loader';
import { getSpecificMovieArticle } from '../../../utils/requests';

export const DetailsContextProvider = createContext('NO CURRENTLY PROVIDED DATA');


function Details() {
    const { movieId } = useParams();
    const [details, dispatch] = useReducer(reducer, INITIAL_MOVIE_DETAILS_STATE);

    useEffect(() => {
        getSpecificMovieArticle(movieId)
            .then(movieDetails => {
                return dispatch({
                    type: REDUCER_ACTIONS.GET_MOVIE_ARTICLE,
                    payload: {
                        _ownerId: movieDetails._ownerId,
                        title: movieDetails.title,
                        description: movieDetails.description,
                        img: movieDetails.img,
                        _id: movieDetails._id,
                        price: movieDetails.price,
                    }
                })
            });
    }, [movieId]);

    return (
        <Fragment>
            {details.isMovieLoading
                ? <Loader />
                : <section>
                    <div className={classes.movieContainer}>
                        <h1 className={classes.movieDetailsTitle}>Movie title: {details.movieArticle.title}</h1>
                        <img className={classes.movieDetailsImg} src={details.movieArticle.img} alt="movie" />
                        <div className={classes.movieDetailsInfo}>
                            <h3 className={classes.movieDetailsInfoImg}>Movie Description</h3>
                            <p className={classes.movieDetailsInfoParagraph}>
                                {details.movieArticle.description}
                            </p>
                            <DetailsContextProvider.Provider value={{
                                ...details,
                                dispatch
                            }}>
                                <DetailsButtons />
                                <CommentSection />
                            </DetailsContextProvider.Provider>
                        </div>
                    </div>
                </section>
            }
        </Fragment>
    );
}


export default Details;
