import classes from './CartItem.module.css';
import { useDispatch } from 'react-redux';

import { cartSliceActions } from '../../../../store/cart-slice';

function CartItem({
    title,
    description,
    img,
    quantity,
    price,
    _id
}) {
    const dispatch = useDispatch();

    return (
        <div className={classes.cartItem}>
            <img className={classes.cartItemImg} src={img} alt="item" />
            <div className={classes.cartItemDetails}>
                <p className={classes.cartItemTitle}>{title}</p>
                <p className={classes.cartItemDescription}>{description}</p>
                <p className={classes.cartItemPrice}>$ {price}</p>
            </div>
            <div className={classes.cartActions}>
                <div className={classes.cartQuantity}>
                    <button onClick={() =>
                        dispatch(cartSliceActions.addNewCartItem({
                            _id,
                            title,
                            description,
                            price,
                            img,
                            quantity
                        }))} className={classes.manipulate}>&#8593;</button>
                    <label htmlFor="qty">{quantity}</label>
                    <button onClick={() => dispatch(cartSliceActions.decreaseCartItemQuantity(_id))} className={classes.manipulate}>&#8595;</button>
                </div>
                <button onClick={() => dispatch(cartSliceActions.deleteCartItem(_id))} className={classes.cartDelete}>
                    &#10060;
                </button>
            </div>
        </div>
    )
};

export default CartItem;