async function request(URL, configOptions) {
    try {
        const response = await fetch(URL, configOptions);

        if (!response.ok) {
            throw new Error((await response.json()).message || 'Something went wrong!');
        } else {
            const data = await response.json();

            return data;
        }
    } catch (error) {
        alert(error.message);
    }
}

function setOptions(method, authToken, body) {
    let options = {
        method,
        headers: {},
    }

    if (authToken) {
        options.headers['X-Authorization'] = authToken;
    }

    if (body) {
        options.headers['Content-Type'] = 'application/json';
        options.body = JSON.stringify(body);
    }

    return options;
}

const baseURL = 'https://cars-react-app-server.herokuapp.com';

async function get(endpoint) {
    return await request(baseURL + endpoint, setOptions('GET'));
}

async function post(endpoint, authToken, data) {
    return await request(baseURL + endpoint, setOptions('POST', authToken, data));
}

async function put(endpoint, authToken, data) {
    return await request(baseURL + endpoint, setOptions('PUT', authToken, data));
}

async function del(endpoint, authToken) {
    return await request(baseURL + endpoint, setOptions('DELETE', authToken));
}

async function patch(endpoint, authToken, data) {
    return await request(baseURL + endpoint, setOptions('PATCH', authToken, data));
}

export {
    get,
    post,
    put,
    del,
    patch
}

export default request;