import { createSlice } from '@reduxjs/toolkit';


const cartSlice = createSlice({
    name: 'cart-slice',
    initialState: {
        items: [],
        totalPrice: 0,
        totalQuantity: 0,
    },
    reducers: {
        setCartItems(state, action) {
            state.items = action.payload.items;
            state.totalPrice = action.payload.totalPrice;
            state.totalQuantity = action.payload.totalQuantity;
        },
        removeCartItems(state) {
            state.items = [];
            state.totalPrice = 0;
            state.totalQuantity = 0;
        },
        addNewCartItem(state, action) {
            const newCartItem = action.payload;
            const selectedItem = state.items.find(item => item._id === newCartItem._id);

            if (!selectedItem) {
                state.items.push(newCartItem);
                state.totalPrice += Number(newCartItem.price);
            } else {
                state.totalPrice += Number(selectedItem.price);
                selectedItem.quantity++;
            }
            state.totalQuantity++;
        },
        deleteCartItem(state, action) {
            const item = state.items.find(item => item._id === action.payload);
            state.items = state.items.filter(item => item._id !== action.payload);

            state.totalQuantity -= Number(item.quantity);
            state.totalPrice -= Number(item.price) * Number(item.quantity);
        },
        decreaseCartItemQuantity(state, action) {
            const item = state.items.find(item => item._id === action.payload);

            state.totalPrice -= Number(item.price);
            item.quantity--;
            state.totalQuantity--;
        }
    }
});

export const cartSliceActions = cartSlice.actions;

export default cartSlice;