import { Fragment } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { deleteMovieArticle } from '../../../../../utils/requests';

import classes from './OwnerButtons.module.css';

function OwnerButtons({ authToken, movieId }) {
    const history = useHistory();

    const deleteSpecificMovieArticle = async (e) => {
        await deleteMovieArticle(movieId, authToken);
        history.push('/');
    }

    return (
        <Fragment>
            <button onClick={deleteSpecificMovieArticle} className={[classes.movieDetailsBtnDelete, classes.movieBtn].join(' ')}>&#10060; Delete</button>
            <Link to={`/movies/edit/${movieId}`} className={[classes.movieDetailsBtnEdit, classes.movieBtn].join(' ')}>&#9997; Edit</Link>
        </Fragment>
    )
}

export default OwnerButtons;
