import { createSlice } from '@reduxjs/toolkit';

const initialAuthState = {
    isAuthenticated: false,
    userAuthState: {},
}

export const authenticate = (authType, userDataObj) => {
    return async function(dispatch) {
        try {
            const response = await fetch(`https://cars-react-app-server.herokuapp.com/users/${authType.toLowerCase()}`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(userDataObj)
            });
            const responseData = await response.json();

            if (response.ok === false) {
                throw new Error('Failed while authenticating!');
            } else {
                dispatch(authSlice
                    .actions
                    .handleAuthenticate({
                        authToken: responseData.accessToken,
                        userId: responseData._id,
                        username: responseData.username
                    }));
            }
        } catch (error) {
            alert(error.message);
        }
    }
}

const authSlice = createSlice({
    name: 'auth-slice',
    initialState: initialAuthState,
    reducers: {
        handleAuthenticate(state, action) {
            state.userAuthState = {
                authToken: action.payload.authToken,
                userId: action.payload.userId,
                username: action.payload.username
            };
            state.isAuthenticated = true;
        },
        handleLogout(state) {
            state.userAuthState = {
                authToken: null,
                userId: null,
                username: null
            };
            state.isAuthenticated = false;
        },
        setUserState(state, action) {
            state.isAuthenticated = action.payload.isAuthenticated;
            state.userAuthState = action.payload.userAuthState;
        }
    },
});

export const authSliceActions = authSlice.actions;

export default authSlice;