import MoviesForum from './MoviesForum/MoviesForum';
import MovieCard from './MovieCard/MovieCard';

import Loader from '../../Common/Loader/Loader';
import { useEffect, useState } from 'react';

import { getMovieArticles } from '../../../utils/requests';

function Home() {
    const [movieArticles, setMoviesArticles] = useState([]);
    const [isLoading, setIsLoading] = useState(true); 

    useEffect(() => {
        getMovieArticles()
            .then(movies => {
                setMoviesArticles(movies);
                setIsLoading(false);
            })
            .catch(err => alert(err.message))
    }, []);

    if (isLoading) {
        return <Loader />
    }

    return (
        <>
            <MoviesForum />
            <section>
                <div>
                    <h1 style={{
                        fontSize: 40,
                        fontWeight: 550,
                        textAlign: 'center',
                        marginTop: 40
                    }}>Movie Articles</h1>
                    <div className="card-wrapper">
                        {
                            movieArticles.map((movieProps, index) => <MovieCard key={index} {...movieProps} />)
                        }
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;